.footer {
  &__adaptive-block {
    background-color: #131313;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd {
      display: none; } }

  &__adaptive-logo-wrap {
    position: relative;
    a {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #DA7B26;
      padding: 35px 80px;
      text-decoration: none;
      color: #fff;
      transition: all 0.4s ease;
      &:hover {
        color: #000;
        &::after {
          transform: translateX(0); }
        p::after {
          width: calc( 100% - 9px ); } } }
    p {
      position: relative;
      font-size: 24px;
      font-weight: 600;
      line-height: 29.4px;
      letter-spacing: 0.4em;
      text-align: left;
      &::after {
        content: '';
        position: absolute;
        width: 77px;
        right: 9px;
        height: 3px;
        background-color: black;
        bottom: -5px;
        transition: all 0.3s; }
      span {
        color: black; } } }

  &__adaptive-col-second {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 30px;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd; }

  &__adaptive-col-second-number {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    h5 {
      color: #fff;
      font-size: 13px;
      margin-bottom: 10px;
      @include min_sm;
      @include min_md {
        font-size: 20px;
        font-weight: 400;
        line-height: 34px;
        text-align: left; }
      @include min_lg;
      @include min_xl;
      @include min_xxl;
      @include min_fullHd; }
    p {
      font-size: 34px;
      font-weight: 900;
      line-height: 35px;
      color: #DA7B26;
      margin: 0;
      @include min_sm;
      @include min_md {
        font-size: 65px;
        font-weight: 900;
        line-height: 67.26px;
        text-align: left;
        color: #DA7B26; }
      @include min_lg;
      @include min_xl;
      @include min_xxl;
      @include min_fullHd;
      span {
        color: #fff; } } }


  &__adaptive-col-second-number-medium {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 20px;
    h5 {
      color: #fff;
      font-weight: 400;
      font-size: 13px;
      @include min_sm;
      @include min_md {
        font-size: 20px;
        line-height: 34px;
        text-align: left; }
      @include min_lg;
      @include min_xl;
      @include min_xxl;
      @include min_fullHd; }
    p {
      margin: 0;
      font-size: 28px;
      font-weight: 900;
      color: #DA7B26;
