  &__adaptive-col-second-txt-wrap {
    position: relative;
    display: flex;
    margin-bottom: 18px;
    @include min_sm;
    @include min_md {
      margin-bottom: 30px; }
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd;
