.main-section {
  position: relative;
  padding-top: 220px;
  // min-height: 100vh
  // +min_fullHd
  //   padding-top: 370px

  // &__full-screen

  // &__block

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    line-height: 24.5px;
    text-align: left;
    color: #000;
    text-decoration: none;
    transition: all 0.5s ease;
    padding: 34px 0 34px 20px;
    @include min_xl {
      padding: 130px 0 130px 100px; }
    @include min_xxl {
      display: block;
      padding: 130px 0 130px 100px; }
    @include min_fullHd {
      display: none; }
    &:hover {
      color: #fff; }
    svg {
      margin-left: 5px; } }

  &__link-more {
    padding: 34px 0;
    align-items: center;
    gap: 8px;
    font-size: 20px;
    font-weight: 600;
    line-height: 24.5px;
    text-align: left;
    color: #0B0B0B;
    text-decoration: none;
    transition: all 0.5s ease;
    display: none;
    &:hover {
      color:  #DA7B26; }
    @include min_sm;
    @include min_md {
 }      // padding: 130px 0 130px 100px
    @include min_lg {
 }      // padding: 130px 0 130px 100px
    @include min_xl {
 }      // padding: 130px 0 130px 100px
    @include min_xxl {
 }      // padding: 130px 0 130px 100px
    @include min_fullHd {
      display: block;
      padding: 130px 0 130px 100px; } }

  &__svg-wrap {
    display: flex;
    justify-content: center;
    svg {
      position: absolute;
      top: 50px;
      z-index: -999;
      height: 1367px;
      width: 1050; } }

  &__title-wrap {
    text-align: center;
    font-weight: 900;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd; }

  &__title-line {
    margin: 0;
    @include min_sm;
    @include min_md {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row; }
    @include min_lg {
      display: flex; }
    @include min_xl {
      display: flex;
      margin-bottom: 20px; }
    @include min_xxl {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0; }
    @include min_fullHd {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      margin: 0; } }

  &__title-line-second {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    @include min_sm;
    @include min_md {
      display: flex; }
    @include min_lg {
      display: flex; }
    @include min_xl {
      display: flex;
      gap: 20px; }
    @include min_xxl {
      display: flex; }
    @include min_fullHd {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 19px; } }

  &__title-bold {
    color: #000;
    font-size: 45px;
    line-height: 46px;
    @include min_sm {
      font-size: 45px;
      line-height: 46px; }
    @include min_md {
      font-size: 58px; }
    @include min_lg {
      font-size: 80px; }
    @include min_xl {
      font-size: 90px;
      line-height: 170px; }
    @include min_xxl {
      font-size: 120px;
      line-height: 180px; }
    @include min_fullHd {
      font-size: 160px;
      font-weight: 900;
      line-height: 183.52px;
      text-align: left; } }

  &__title-bold-second {
    color: #000;
    font-weight: 900;
    font-size: 33px;
    line-height: 34px;
    @include min_sm {
      font-size: 33px;
      line-height: 34px; }
    @include min_md {
      font-size: 58px; }
    @include min_lg;
    @include min_xl {
      font-size: 85px; }
    @include min_xxl {
      font-size: 110px; }
    @include min_fullHd {
      font-size: 155px;
      font-weight: 900;
      line-height: 183px;
      text-align: left; } }


  &__title-outlined {
    color: #fff;
    -webkit-text-stroke: 2px #000;
    font-size: 78.39px;
    line-height: 81.13px;
    text-align: left;
    @include min_sm {
      font-size: 78.39px;
      line-height: 81.13px;
      text-align: left; }
    @include min_md {
      font-size:  58px;
      line-height: 120px; }
    @include min_lg {
      font-size: 80px;
 }      // line-height: 160px
    @include min_xl {
      font-size: 90px;
      line-height: 170px; }
    @include min_xxl {
      font-size: 120px;
      line-height: 180px; }
    @include min_fullHd {
      font-size: 160px;
      line-height: 183.52px;
      font-weight: 900;
      text-align: left; } }

  &__title-accent {
    color:  #DA7B26;
    font-weight: 900;
    text-align: left;
    font-size: 33.33px;
    line-height: 34.49px;
    text-align: left;
    @include min_sm {
      font-size: 33.33px;
      line-height: 34.49px;
      text-align: left; }
    @include min_md {
      font-size: 58px; }
    @include min_lg;
    @include min_xl {
      font-size: 85px; }
    @include min_xxl {
      font-size: 110px; }
    @include min_fullHd {
      font-size: 155px;
      line-height: 183px; } }


  &__title-icon {
    display: none;
    @include min_sm {
      display: none; }
    @include min_md {
      display: none; }
    @include min_lg {
      display: none; }
    @include min_xl {
      display: none; }
    @include min_xxl {
      display: block; }
    @include min_fullHd {
      display: block;
 } }      // margin-bottom: 20px

  &__subtitle-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    margin-top: 40px;
    margin-bottom: 80px;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd;
    span {
      letter-spacing: 0.58em;
      text-align: center;
      font-size: 20px;
      line-height: 20px;
      letter-spacing: 5%;
      @include min_sm {
        letter-spacing: 0.58em;
        text-align: center;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 20%; }
      @include min_md;
      @include min_lg {
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 10px; }
      @include min_xl;
      @include min_xxl;
      @include min_fullHd {
        font-size: 26px;
        font-weight: 500;
        line-height: 31.54px;
        letter-spacing: 0.58em;
        text-align: center; } } }

  &__link-learn-more {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 148px;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom:  9.885vh; } }

  &__two-block {
    margin-bottom: 9.885vh;
    position: relative;
    // height: 290px
    // height: 290px
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd {
      display: flex; }
    span {
      font-size: 34px;
      line-height: 58px;
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 1;
      text-align: left;
      font-weight: 900;
      color: transparent;
      -webkit-text-stroke: 1px #BCB6B6E5;
      transform: translate(-50%,-50%);
      @include min_sm;
      @include min_md {
        font-size: 70px;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        text-align: left;
        font-weight: 900;
        color: transparent;
        -webkit-text-stroke: 1px #BCB6B6E5;
        transform: translate(-50%,-50%); }

      @include min_lg;
      @include min_xl {
        font-size: 70px;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        text-align: left;
        font-weight: 900;
        color: transparent;
        -webkit-text-stroke: 1px #BCB6B6E5;
        transform: translate(-50%,-50%); }
      @include min_xxl {
        font-size: 136px;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        text-align: left;
        font-weight: 900;
        color: transparent;
        -webkit-text-stroke: 1px #BCB6B6E5;
        transform: translate(-50%,-50%);
        // font-size: 177px
 }        // line-height: 183px
      @include min_fullHd {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 10;
        text-align: left;
        font-weight: 900;
        color: transparent;
        -webkit-text-stroke: 1px #BCB6B6E5;
        transform: translate(-50%,-50%);
        font-size: 177px;
        line-height: 183px; } } }

  &__first-block {
    flex: 1 1 50%;
    max-width: 100%;
    background:  #DA7B26;
    height: 88px;
    @include min_lg;
    @include min_xl {
      height: 290px; }
    @include min_xxl;
    @include min_fullHd {
      height: 23.9vh;
      max-width: 50%;
      background:  #FBFAFA; } }

  &__second-block {
    flex: 1 1 50%;
    max-width: 50%;
    background-image: url("../assets/images/background.png");
    background-repeat: no-repeat;
    background-size: cover;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd {
      width: 100%; } } }













