.header {
  position: fixed;
  width: 100%;
  z-index: 999;
  background: #FBFAFA;

  &__adaptive-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    @include min_xl {
      display: none; } }

  button {
    background-color: #454545;
    width: 5.625rem;
    height: 5.625rem; }

  &__adaptive-basket-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #DA7B26;
    width: 5.625rem;
    height: 5.625rem; }

  &__logo-adaptive-wrap {
    position: relative;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #0B0B0B;
      width: 12.5rem;
      height: 5.625rem;
      text-decoration: none;
      color: #fff;
      transition: all 0.4s ease;
      &:hover {
        background: #282828;
        color: #da7b26;
        &::after {
          transform: translateX(0); }
        p::after {
          width: calc( 100% - .5625rem ); } } }
    p {
      position: relative;
      font-size: 1rem;
      font-weight: 600;
      line-height: 1.275rem;
      letter-spacing: 0.4em;
      text-align: left;
      &::after {
        content: '';
        position: absolute;
        width: 4.8125rem;
        right: .5625rem;
        height: .125rem;
        background-color: #da7b26;
        bottom: -0.3125rem;
        transition: all 0.3s; } } }

  &__block {
    display: none;
    @include min_xl {
      display: flex;
      justify-content: space-between; } }

  &__nav {
    display: flex;
    align-items: center; }

  &__basket-lang-wrap {
    display: flex; }

  &__basket-svg-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.5rem;
    height: 1.5rem;
    svg {
      width: 100%;
      height: 100%; } }
  &__list {
    display: flex;
    // gap: 5rem
    @include min_xl {
      gap: 4rem; }
    @include min_xxl {
      gap: 5.3125rem; }
    a {
      position: relative;
      text-decoration: none;
      display: inline-block;
      color:  #0B0B0B;
      // font-size: 1.5rem
      font-weight: 600;
      // line-height: 1.8375rem
      text-align: left;
      overflow: hidden;
      padding-bottom: .625rem;
      transition: all 0.5s ease;
      @include min_xl {
        font-size: 1rem;
        line-height: 1.225rem; }
      @include min_xxl {
        font-size: 1.25rem;
        line-height: 1.5313rem; }
      &:hover {
        color: #da7b26;
        &::after {
          transform: translateX(0); } }
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        left: 0rem;
        height: .1875rem;
        background-color: #da7b26;
        bottom: 0;
        transition: transform 0.3s ease;
        transform: translateX(calc(-100% + 1.875rem)); } } }


  &__logo-wrap {
    position: relative;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #0B0B0B;
      text-decoration: none;
      color: #fff;
      transition: all 0.4s ease;
      @include min_xl {
        padding: 2.25rem 2.625rem; }
      @include min_xxl {
        padding: 3.125rem 3.75rem; }

      &:hover {
        background: #282828;
        color: #da7b26;
        // &::after
        //   transform: translateX(0)
        p::after {
          width: calc( 100% - .25rem ); } } }
    p {
      position: relative;
      font-family: $deff;
      text-align: left;
      margin-bottom: 0;
      @include min_xl {
        font-size: .8rem;
        font-weight: 600;
        line-height: .98rem;
        letter-spacing: 0.4em; }
      @include min_xxl {
        font-size: 1.125rem;
        line-height: 1.375rem; }
      @include min_fullHd {
        font-size: 1.5rem;
        line-height: 1.8375rem; }

      &::after {
        content: '';
        position: absolute;
        right: .25rem;
        height: .125rem;
        background-color: #da7b26;
        bottom: -0.3125rem;
        transition: all 0.3s;
        @include min_xl {
          width: 2.5625rem; }
        @include min_xxl {
          width: 3.75rem; }
        @include min_fullHd {
          width: 4.8125rem; } }
      span {
        color: #da7b26; } } }

  &__lang-wrap {
    display: flex;
    justify-content: center;
    flex-direction: column;
    // padding-right: 3.875rem
    // padding-left: 3.8125rem
    background: #454545;
    transition: all 0.4s ease;
    @include min_xl {
      padding: 2.125rem 1.875rem; }
    @include min_xxl {
      padding: 3.1875rem 2.6875rem; }

    &:hover {
      background: #555454; }
    a {
      font-family: $deff;
      font-weight: 400;
      text-align: left;
      text-decoration: none;
      color: #FFFFFF;
      @include min_xl {
        font-size: 1rem;
        line-height: 1.1875rem; }
      @include min_xxl {
        font-size: 1.25rem;
        line-height: 1.8375rem; } } }

  &__basket-wrap {
      position: relative;
      background: #DA7B26;
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      @include min_xl {
        align-items: center;
        padding-left: 1.5625rem;
        padding-right: 1.375rem;
        gap: 6.4375rem; }
      @include min_xxl {
        align-items: center;
        gap: 6.6875rem;
        padding-left: 2.9375rem;
        padding-right: 2.9375rem; }
      &:hover {
        background-color: #F2913A; }

      span {
        // padding-top: .3125rem
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        @include min_xl;
        @include min_xxl {
          font-size: 1.25rem;
          font-weight: 600;
          line-height: 1.5313rem;
          text-align: left; }
        // +min_fullHd
        //   display: block
        //   padding-top: .25rem
        //   font-size: 1.5rem
        //   font-weight: 600
        //   line-height: 1.8375rem
 }        //   text-align: left
      &:after {
        // +min_fullHd
        display: block;
        content: "";
        position: absolute;
        // width: 7.8125rem
        height: .0625rem;
        top: 50%;
        // left: 7.5rem
        background-color: #ffff;
        @include min_xl {
          width: 4.125rem;
          left: 4.375rem; }
        @include min_xxl {
          width: 4.5625rem;
          left: 5.25rem; } } } }









