// Typography
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

::selection {
	background-color: $c1;
	color: #fff; }

ul {
	margin: 0;
	padding: 0;
	list-style-type: none; }

a {
	transition: all .3s ease;
	&:hover {
		text-decoration: none; }
	&:focus {
		text-decoration: none;
		outline: none; } }

img {
	width: auto;
	max-width: 100%;
	height: auto; }

// Titles
h1, h2, h3, h4, h5, h6 {
  margin: 0; }

h1 {
	font-size: 36px; }
h2 {
	font-size: 30px; }
h3 {
	font-size: 24px; }
h4 {
	font-size: 18px; }
h5 {
	font-size: 14px; }
h6 {
	font-size: 12px; }

