.top-product {
  @include min_sm;
  @include min_md;
  @include min_lg;
  @include min_xl;
  @include min_xxl;
  @include min_fullHd;

  &__title-wrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 30px;
    @include min_sm {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      gap: 50px; }
    @include min_md {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px; }
    @include min_lg {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
    @include min_xl {
      display: flex;
      align-items: center;
      flex-direction: column;
      // justify-content: space-around
      margin-bottom: 80px; }
    @include min_xxl {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 80px; }
    @include min_fullHd {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 80px; }
    a {
      display: none;
      @include min_sm {
        display: none; }
      @include min_md {
        display: none; }
      @include min_lg {
        display: block; }
      @include min_xl {
        display: block; }
      @include min_xxl {
        display: block; }
      @include min_fullHd {
        display: block; } }
    h2 {
      text-transform: uppercase;
      font-size: 30px;
      font-weight: 900;
      line-height: 31.05px;
      text-align: center;
      @include min_sm {
        text-align: center;
        font-size: 30px;
        line-height: 40px; }
      @include min_md {
        text-align: center;
        font-size: 65px;
        line-height: 60px; }
      @include min_lg {
        text-align: center;
        font-size: 80px;
        line-height: 75px; }
      @include min_xl {
        font-size: 85px; }
      @include min_xxl {
        font-size: 90px;
        line-height: 80px; }
      @include min_fullHd {
        text-transform: uppercase;
        font-size: 120px;
        font-weight: 900;
        line-height: 108px;
        text-align: left; } } }

  &__items-wrap {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 585px;
    @include xxl {
      grid-template-columns: repeat(2, 1fr); }
    @include lg {
      grid-template-columns: repeat(1, 1fr); }
    @include sm {
      grid-auto-rows: 226px; } }

  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover; }

  &__pos-img {
    display: none;
    @include min_sm;
    @include min_md;
    @include min_lg;
    @include min_xl;
    @include min_xxl;
    @include min_fullHd {
        display: block; } }

  &__item {
    display: flex;
    align-items: flex-end;
    position: relative;
    border: 1px solid #D0D0D0;
    transition: all 0.5s ease;
    cursor: pointer;
    z-index: 1;
    @include sm {
      justify-content: center; }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-image: url('../assets/images/top-backg.png');
      background-size: cover;
      background-repeat: no-repeat;
      width: 288px;
      height: 381px;
      z-index: -1;
      @include sm {
        width: 133px;
        height: 176px; } }

    &:hover {
      background-color: #DA7B26;
      span {
        color: #FFFF; } }

    a {
      position: absolute;
      bottom:  110px;
      left: 111px;
      svg {
        margin-left: 15px; } }


    h5 {
      text-transform: uppercase;
      font-size: 24px;
      font-weight: 600;
      line-height: 29.71px;
      text-align: left;
      margin: 0 0 55px 68px;
      @include sm {
        font-size: 18px;
        line-height: 22px;
        margin: 0 0 22px 0;
        text-align: center; }

      span {
        transition: all 0.5s ease;
        color: #DA7B26; } } }
  &__img {
    z-index: 10; }

  &__item-orange {
    position: relative;
    background-color: #DA7B26;
    img {
      position: absolute;
      top: 26px;
      left: 100px;
      @include min_sm {
        top:  100px;
        left: 110px; }
      @include min_md {
        top:  100px;
        left: 150px; }
      @include min_lg {
        top:  100px;
        left: 90px; }
      @include min_xl;
      @include min_xxl {
        top:  100px;
        left: 100px; }
      @include min_fullHd {
        top:  100px;
        left: 150px; } }
    h4 {
      text-transform: uppercase;
      position: absolute;
      color: #fff;
      font-size: 24.18px;
      font-weight: 800;
      line-height: 26.6px;
      top: 61px;
      left: 58px;
      @include min_sm {
        font-size: 40px;
        font-weight: 800;
        line-height: 55px;
        text-align: left;
        top: 230px;
        left: 90px; }
      @include min_md {
        font-size: 40px;
        font-weight: 800;
        line-height: 55px;
        text-align: left;
        top: 230px;
        left: 90px; }
      @include min_lg {
        font-size: 40px;
        font-weight: 800;
        line-height: 55px;
        text-align: left;
        top: 230px;
        left: 90px; }
      @include min_xl {
        font-size: 40px;
        font-weight: 800;
        line-height: 55px;
        text-align: left;
        top: 230px;
        left: 100px; }
      @include min_xxl {
        font-size: 40px;
        font-weight: 800;
        line-height: 55px;
        text-align: left;
        top: 230px;
        left: 100px; }
      @include min_fullHd {
        font-size: 50px;
        font-weight: 800;
        line-height: 55px;
        text-align: left;
        top: 230px;
        left: 110px; } }
    a {
      position: absolute;
      top: 144px;
      left: 40px;
      @include min_sm {
        top:  390px;
        left: 100px; }
      @include min_md {
        top:  390px;
        left: 100px; }
      @include min_lg {
        top:  390px;
        left: 100px; }
      @include min_xl {
        top:  390px;
        left: 100px; }
      @include min_xxl {
        top: 390px;
        left: 111px; }
      @include min_fullHd {
        top: 480px;
        left: 111px; } } }



  &__pos-img {
    position: absolute;
    top: 95px;
    left: 194px;
    opacity: 0;
    transition: all 0.5s ease;
    // z-index: -1
    &:hover {
      opacity: 1;
      visibility: inherit; } } }






